html, body, #root, *[data-reactroot] {
  min-height: 100vh;
}

*[data-reactroot] {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#root main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-flow: column;
      flex-flow: column;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'sans-serif';
  color: #333;
  background-color: #fdfdfd;
}

.sticky-sidebar {
  list-style: none;
}

@media (min-width: 725px) {
  #root main {
    -ms-flex-flow: row;
        flex-flow: row;
  }

  .sticky-sidebar {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    min-height: 90vh;
  }
}

a[href] {
  color: #975d89;
  text-decoration: none;
}

a[href]:hover {
  border-bottom: 1px dotted #dc8ec9;
}

blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 0.1em 10px 1em 10px;
  padding: 0.5em 10px;
  quotes: "\201C""\201D""\2018""\2019";
  font-size: 90%;
}

blockquote p {
  margin: 0;
}

blockquote p:not(:last-child) {
  padding-bottom: 1rem;
}

*::-moz-selection {
  background-color: #dc8ec9;
}

*::selection {
  background-color: #dc8ec9;
}

ul {
  margin-top: 0.5rem;
  margin-bottom: 0;
}

ul > li {
  margin-bottom: 0.5rem;
}


